<template>
    <div class="news-feed">
        <NewsItem
            v-for="item in newsItems"
            :key="item.id"
            :newsItem="item"></NewsItem>
    </div>
</template>

<script>
export default {
  computed: {
    newsItems: function () {
      return this.$store.state.newsItems
    }
  }
}
</script>

<style lang="scss">
@import '../styles/theme.scss';

    .news-feed {
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        flex-flow: wrap;
        padding: 15px;
        flex-basis: 30%;
    }

    @media screen and (max-width: $mediaMaxWidth) {
      .news-feed {
        flex-direction: column;
        justify-content: flex-start;
      }
    }
</style>



